import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TemplateDataComponent } from './components/template-data/template-data.component';
import { TemplateHomeComponent } from './components/template-home/template-home.component';

const routes: Routes = [
  {
    path: "",
    pathMatch: "prefix",
    children: [
      {
        path: "",
        pathMatch: "full",
        redirectTo: "dashboard"
      },
      {
        path: "dashboard",
        pathMatch: "full",
        component: TemplateHomeComponent
      },
      {
        path: "create",
        pathMatch: "full",
        component: TemplateDataComponent
      },
      {
        path: "edit/:id",
        pathMatch: "full",
        component: TemplateDataComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TemplateRoutingModule { }
