import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-template-feature',
  templateUrl: './template-feature.component.html',
  styleUrls: ['./template-feature.component.css']
})
export class TemplateFeatureComponent implements OnInit {

  @Input("text")
  public text: string = "";

  @Input("location")
  public location: string = "/home";

  constructor(private router: Router, private activeRoute: ActivatedRoute) { }

  ngOnInit(): void {
  }

  navigateToLocation() : void {
    this.router.navigate(['../edit/'+this.location], {relativeTo: this.activeRoute});
  }

}
