import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Template } from 'src/app/common/dto/template';
import { TemplateService } from 'src/app/common/service/template/template.service';

@Component({
	selector: 'app-template-data',
	templateUrl: './template-data.component.html',
	styleUrls: ['./template-data.component.css']
})
export class TemplateDataComponent implements OnInit {

	public templateForm: FormGroup;
	public template: Template | null = null;

	constructor(builder: FormBuilder, private templateService: TemplateService, private toastr: ToastrService,
		private router: Router, private activeRoute: ActivatedRoute) {
		const id: string = activeRoute.snapshot.params.id;
		if (id != null) {
			this.loadTemplate(id);
		}

		this.templateForm = builder.group({
			'name': ['', Validators.required],
			'message': ['', Validators.required]
		});
	}

	loadTemplate(id: string) {
		this.templateService.getById(id).then(
			(template: Template) => {
				this.template = template;
				this.templateForm.patchValue({
					name: template.name,
					message: template.message
				});
				this.templateForm.updateValueAndValidity();
			},
			(reason: any) => {
				this.toastr.error(reason.message);
			}
		)
	}

	ngOnInit(): void {

	}

	onSubmit(): void {
		this.templateForm.markAllAsTouched();
		if (this.templateForm.valid) {
			const template: Template = { ...this.template, ...this.templateForm.value as Template };
			if (template.id == undefined) {
				this.create(template);
			} else {
				this.update(template);
			}
		}
	}

	create(template: Template): void {
		this.templateService.create(template).then(
			(result: boolean) => {
				this.toastr.success("Template was created successfully. You will be redirected to Template Dashboard.");
				this.router.navigate(['../dashboard'], {
					relativeTo: this.activeRoute
				});
			},
			(error: any) => {
				this.toastr.success("Could not create template due to '" + error.message + "'.");
			}
		)
	}

	update(template: Template): void {
		this.templateService.update(template).then(
			(result: boolean) => {
				this.toastr.success("Template was updated successfully. You will be redirected to Template Dashboard.");
				this.router.navigate(['../../dashboard'], {
					relativeTo: this.activeRoute
				});
			},
			(error: any) => {
				this.toastr.success("Could not update template due to '" + error.message + "'.");
			}
		)
	}

	updateMessage(key: string): void {
		const newMessage: string = this.templateForm.get('message')?.value + " " +key+ " ";
		this.templateForm.patchValue({
			message: newMessage
		});
		this.templateForm.updateValueAndValidity();
	}
}
