import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-template-home-nav',
  templateUrl: './template-home-nav.component.html',
  styleUrls: ['./template-home-nav.component.css']
})
export class TemplateHomeNavComponent implements OnInit {

  constructor(private router: Router, private activeRoute: ActivatedRoute) { }

  ngOnInit(): void {
  }

  navigateToHome(): void {
    this.router.navigateByUrl("/home");
  }

  navigateToCreate(): void {
    this.router.navigate(['../create'], {relativeTo: this.activeRoute});
  }

}
